.grid-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding: 10px 10px;
    font-weight: bold;
    font-size: x-large;
  }

  .grid-element {
    /* border-style: solid; */
    border-style: solid;
    /* border-style: outset; */
    border-width: 0.01px;
    box-shadow: 10px 10px 8px #888888;
    border-right: 8px solid blue;
    /* border-bottom: 5px solid blue; */
    padding: 40px 20px;
    text-align: center;
    border-radius: 15px;
  }

  .grid-element:hover {
      box-shadow: 15px 15px 10px #888888;;
  }

  .textStyle {
      text-decoration: underline;
      font-weight: lighter;
      font-size: x-large;
  }

  .mainText {
    /* border: 10px solid black; */
    padding: 5px 5px;
    background: #333;
    color: #fff;
    text-align: center;
    box-shadow: 2px 2px 1px #888888;
    font-weight: lighter;
    font-size: x-large;
    /* padding: '10px'; */
  }

  .mainText:hover {
    box-shadow: 3px 3px 2px #888888;
  }